import React from "react"
import { Link } from "gatsby"
import styled from "styled-components";
import { rhythm } from "../utils/typography"
import { createGlobalStyle } from 'styled-components'

const MOBILE_WIDTH = 600;
const MOBILE_QUERY = `(max-width: ${MOBILE_WIDTH}px)`;

const TitleH1 = styled.h1`
  margin-bottom: ${rhythm(1.5)};
  margin-top: 0;
  @media ${MOBILE_QUERY} {
    text-align: center;
  }
`;

const GlobalStyles = createGlobalStyle`
  ul li {
    margin-left: 16px;
  }

  ul {
    margin-left: 0 !important;
  }

  h3 {
    line-height: 1.4;
  }

  .gatsby-resp-image-wrapper {
    @media ${MOBILE_QUERY} {
      &[style] {
        margin-left: -22px !important;
        margin-right: -22px !important;
      }
    }
  }

  .gatsby-resp-image-figcaption {
    display: flex;

    p {
      margin: 8px auto 0;
      text-align: center;
      font-size: 14px;
      font-style: italic;
    }
  }
`;

const messages = {
  en: {
    title: `Sergey Ulyashev's Blog`,
    author: `Sergey Ulyashev`,
  },
  ru: {
    title: `Блог Сергея Уляшева`,
    author: `Сергей Уляшев`,
  }
}

class Layout extends React.Component<any> {
  render() {
    const { location, children, langKey = 'en' } = this.props
    const { author, title } = messages[langKey];
    const homeLink = langKey === 'en' ? '/' : `/${langKey}`;
    const isMainPage = location.pathname === homeLink;
    let header

    if (isMainPage) {
      header = (
        <TitleH1>
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={homeLink}
          >
            {title}
          </Link>
        </TitleH1>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Montserrat, sans-serif`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={homeLink}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        <GlobalStyles />
        <header>{header}</header>
        <main>{children}</main>
        <footer style={{ marginTop: isMainPage ? 80 : 0 }}>
          {new Date().getFullYear()}
          {` © `}
          {
            isMainPage
            ? author
            : <Link to={langKey === 'ru' ? '/ru' : '/'}>{author}</Link>
          }
        </footer>
      </div>
    )
  }
}

export default Layout
